import './App.css';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import { Link } from 'react-router-dom';
import { Topics } from './Topics';

function App() {
  return (
    <Router>
    <div className="App">
      <ul>
        <li>
          <Link to="/">Home</Link>
        </li>
        <li><Link to="/topics">Topics</Link></li>
      </ul>
      <hr></hr>     
      <Switch>
        <Route exact path="/">
          <Home/>
        </Route>
        <Route path="/topics">
          <Topics/>
        </Route>
      </Switch>
    </div>
    </Router>
  );
}

export default App;

function Home () { 
  return(
    <div>
      <h1>Home page</h1>
    </div>
  )
}

